.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  background-color: #0000005e;
  backdrop-filter: blur(4px);

  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  z-index: -2;
  transition: opacity 0.3s ease-in, z-index 0.1s ease-in;
  &.active {
    z-index: 1000;
    opacity: 1;
  }

  &__body {
    position: relative;
    padding: 3rem 2rem;
    background: #1d1b1a;
    width: 100%;
    max-width: 56rem;
  }

  &__images {
    display: flex;
    justify-content: center;
    margin-bottom: 1.6rem;
  }

  &__img {
    width: 7rem;
    aspect-ratio: 1 / 1;
    flex-shrink: 0;
    &:not(:last-child) {
      margin-right: -1rem;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 3rem;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 3rem;
    line-height: 110%;
    letter-spacing: -0.01em;
    text-align: center;
    color: #f5f5f5;
  }

  &__close {
    position: absolute;
    width: 3.5rem;
    aspect-ratio: 1 / 1;
    z-index: 4;
    right: 1rem;
    top: 1rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;

    background-image: url("./../img/zay/cross.svg");
    background-size: 1.6rem 1.6rem;
    background-position: center;
    background-repeat: no-repeat;
  }
}
